.contact-form-container .contact-title{
    color: #4D5766;
    font-size: 27px;
    font-weight: lighter;
    margin-top: 30px;
    margin-bottom: 30px;
}
.form-border{
    border: 1px solid #E0E0E0;
    border-radius: 6px;
}
#standard-dense, .MuiInput-input{
    box-sizing: content-box;
}
.contact-component-container .top-border{
    border: none;
}
#contact-form input{
    box-sizing: content-box;
}

@media (max-width: 960px) {
    #contact-form .mobile-input{
        width: 100%;
    }
    #contact-form .form-containers{
        flex-direction: column;
        padding: 0;
        margin-top: 0;
    }
    #contact-form .form-border{
        padding-left: 1rem;
        padding-right: 1rem;
        border: none;
        padding-top: 0;
    }
    
}
@media (max-width: 600px) {
    .contact-form-container #contact-form{
        margin: 0;
    }
    .contact-form-container #contact-form > .form-border{
        padding: 0;
    }
    .contact-form-container .form-containers > div{
        margin-left: 0;
        margin-right: 0;
    }
}